/* 기본 스타일은 유지하면서 애니메이션 클래스 추가 */
.appear {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.hidden {
  opacity: 0;
  transform: translateY(20px);
}

/* 다른 스타일은 그대로 유지 */
.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.section {
  padding: 60px 20px;
  width: 100%;
  box-sizing: border-box;
}

.hero-section {
  /* background-image: url('/images/hero-background.jpg'); */
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
}

.hero-section h1 {
  font-size: 4em;
}

.hero-section p {
  font-size: 1.5em;
}

.gallery-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gallery-section h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.gallery img {
  margin: 10px;
  width: 200px;
  height: auto;
  border: 2px solid #ddd;
  border-radius: 5px;
}

.circle-menu {
  position: fixed;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 5px;
  z-index: 1000;
}

.circle-menu button {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: none;
  background-color: #333;
  color: white;
  font-size: 0.8em;
  cursor: pointer;
  transition: background-color 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
}

.circle-menu button span {
  display: none;
  position: absolute;
  right: 30px;
  background-color: #333;
  color: white;
  padding: 5px;
  border-radius: 3px;
  white-space: nowrap;
}

.circle-menu button:hover span {
  display: inline-block;
}

.circle-menu button:hover {
  background-color: #555;
}

.welcome-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.welcome-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome-img {
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.welcome-text {
  max-width: 800px;
}

.welcome-text h2 {
  font-size: 2em;
  margin-bottom: 20px;
}

.welcome-text p {
  font-size: 1em;
  margin-bottom: 20px;
}

.welcome-link {
  font-size: 1em;
  color: #0073e6;
  text-decoration: none;
}

.welcome-link:hover {
  text-decoration: underline;
}

.footer-section {
  padding: 20px;
  text-align: center;
  background-color: #fff;
  border-top: 1px solid #eaeaea;
}

.footer-section p {
  font-family: 'Lora', serif;
  font-size: 14px;
  color: #555;
  margin: 0;
}

.footer-icons {
  margin-top: 10px;
}

.footer-icons a {
  color: #555;
  text-decoration: none;
  font-size: 24px;
  transition: color 0.3s;
}

.footer-icons a:hover {
  color: #000;
}
