.login {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://images.pexels.com/photos/22033614/pexels-photo-22033614.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.loginTitle {
  font-size: 50px;
}

.loginForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.loginForm > label {
  margin: 10px 0;
}

.loginInput {
  padding: 10px;
  background-color: white;
  border: none;
  border-radius: 10px;
}

.loginInput:focus {
  outline: none;
}

.loginButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: lightcoral;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 10px;
  text-align: center;
}

.loginRegisterButton {
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: teal;
  cursor: pointer;
  padding: 10px;
  border: none;
  color: white;
  border-radius: 10px;
}

.loginRegisterButton a {
  text-decoration: none;
}

/* 미디어 쿼리 추가 */
@media (max-width: 768px) {
  .login {
    background-size: cover;
    background-position: center;
    height: 100vh;
  }

  .loginTitle {
    font-size: 36px;
  }

  .loginForm {
    width: 80%;
  }

  .loginInput {
    padding: 8px;
    font-size: 16px;
  }

  .loginButton {
    padding: 8px;
    font-size: 16px;
  }

  .loginRegisterButton {
    padding: 8px;
    font-size: 14px;
  }
}
