.post {
  width: 385px;
  margin: 0 auto 40px auto; 
  display: flex;
  flex-direction: column;
  align-items: center; 
}

.postImg {
  width: 385px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
  margin-bottom: 15px; 
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center; 
}

.postCats {
  display: flex;
  justify-content: center; 
}

.postCat {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #708238;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
  text-decoration: none; 
}

.postTitle {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  cursor: pointer;
  text-decoration: none; 
}

.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #999999;
  margin-top: 15px;
}

.postDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 15px;
  text-align: center; 
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.link {
  text-decoration: none; 
  color: inherit; 
}

.link:hover {
  color: #708238; 
}

.postCat, .postTitle {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #708238; 
  border-radius: 3px;
  transition: background-color 0.3s, color 0.3s;
}

.postCat:hover, .postTitle:hover {
  background-color: #708238; 
  color: white;
}

.link:hover {
  color: white;
}
