.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f9f9f9;
  padding: 20px;
  box-sizing: border-box;
}

.contactWrapper {
  width: 100%;
  max-width: 800px;
  padding: 20px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.contactTitle {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: teal;
}

.contactContent {
  display: flex;
  flex-direction: column;
}

.contactImageWrapper {
  flex: 1;
  border-radius: 10px;
  height: 200px;
  margin-bottom: 20px;
}

.contactImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.contactForm {
  flex: 2;
  display: flex;
  flex-direction: column;
  margin-left: 0;
}

.contactForm > label {
  margin: 10px 0 5px;
  font-size: 1.1rem;
  color: #333;
}

.contactForm > input,
.contactForm > textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
}

.contactForm > input[readonly] {
  background-color: #f9f9f9;
  color: #333;
}

.contactForm > textarea {
  resize: none;
  height: 100px;
}

@media (min-width: 768px) {
  .contactContent {
    flex-direction: row;
  }

  .contactImageWrapper {
    height: auto;
    margin-bottom: 0;
  }

  .contactForm {
    margin-left: 20px;
  }
}
