.services {
  width: 100%;
  text-align: center;
  padding: 50px 20px;
}

.servicesTitle {
  font-size: 3em;
  margin-bottom: 10px;
}

.servicesSubtitle {
  font-size: 1.2em;
  margin-bottom: 50px;
}

.serviceItems {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}

.serviceItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 300px;
  margin: 20px;
}

.serviceImg {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 20px;
  object-fit: cover;
}

.serviceItem h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.serviceItem p {
  font-size: 1em;
  color: #555;
  text-align: center;
}
