.top {
  width: 100%;
  height: 50px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
  padding: 0 10px; /* 좌우 패딩 추가 */
  box-sizing: border-box; /* 패딩을 포함한 박스 크기 계산 */
}

.topLeft, .topCenter, .topRight {
  display: flex;
  align-items: center;
}

.topLeft i, .topCenter ul, .topRight ul {
  display: flex;
}

.topCenter ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.topCenter li {
  margin-right: 20px;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer;
}

.topCenter li:last-child {
  margin-right: 0;
}

.topRight ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.topRight li {
  margin-right: 20px;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer;
}

.topRight li:last-child {
  margin-right: 0;
}

.topImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.hamburger {
  display: none;
  cursor: pointer;
  flex-direction: column;
  justify-content: space-between;
  height: 24px;
}

.hamburger div {
  width: 35px;
  height: 4px;
  background-color: black;
  transition: 0.4s;
}

.change.bar1 {
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change.bar2 {
  opacity: 0;
}

.change.bar3 {
  transform: rotate(45deg) translate(-8px, -8px);
}

@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .topCenter ul, .topRight ul {
    flex-direction: column;
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    background-color: white;
    padding: 0;
    margin: 0;
    display: none;
  }

  .topCenter ul.open, .topRight ul.open {
    display: flex;
  }

  .topCenter li, .topRight li {
    width: 100%;
    text-align: center;
    margin: 0;
    padding: 10px 0;
  }
}
